import React from 'react';
import { Todo } from '../../types/Todo';

interface Props {
  todos: Todo[];
  showModal: (arg: boolean) => void;
  selectedTodo: Todo | null;
  selectTodo: (arg: Todo) => void;
}

export const TodoList: React.FC<Props> = (props) => {
  const {
    todos,
    showModal,
    selectedTodo,
    selectTodo,
  } = props;

  return (
    <table className="table is-narrow is-fullwidth">
      <thead>
        <tr>
          <th>#</th>
          <th>
            <span className="icon">
              <i className="fas fa-check" />
            </span>
          </th>
          <th>Title</th>
        </tr>
      </thead>

      <tbody>
        {todos.map(todo => (
          <tr
            data-cy="todo"
            className=""
            key={todo.id}
          >
            <td className="is-vcentered">{todo.userId}</td>
            <td className="is-vcentered">
              {todo.completed && (
                <span className="icon" data-cy="iconCompleted">
                  <i className="fas fa-check" />
                </span>
              )}
            </td>
            <td className="is-vcentered is-expanded">
              <p
                className={todo.completed
                  ? 'has-text-danger'
                  : 'has-text-success'}
              >
                {todo.title}
              </p>
            </td>
            <td className="has-text-right is-vcentered">
              <button
                data-cy="selectButton"
                className="button"
                type="button"
                onClick={() => {
                  selectTodo(todo);
                  showModal(true);
                }}
              >
                <span className="icon">
                  <i className={selectedTodo?.id !== todo.id
                    ? 'far fa-eye'
                    : 'far fa-eye-slash'}
                  />
                </span>
              </button>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};
